*, :before, :after {
  box-sizing: border-box;
}

article {
  margin-bottom: 2rem;
}

[data-text-container] {
  --collapse-line-numbers: 5;
  --line-height: 1.5;
  height: calc(var(--collapse-line-numbers) * var(--line-height) * 1rem);
  position: relative;
  overflow: hidden;
}

[data-expand-container].expand [data-text-container], [data-expand-container][data-overflow="false"] [data-text-container] {
  height: initial;
}

[data-expand-container][data-overflow="false"] [data-expand-button] {
  display: none;
}

[data-expand-container] [data-text-container]:before {
  content: "";
  background: linear-gradient(#0000, #fff);
  position: absolute;
  inset: 0;
}

[data-overflow="false"] [data-text-container]:before, [data-expand-container].expand [data-text-container]:before {
  background: initial;
  display: none;
}
